import React from 'react';
import './App.css';
import NameCard from './components/NameCard';
import DigitalClock from './components/DigitalClock';
import CommentList from './components/CommentList';
import CommentBox from './components/CommentBox';
import ThemeContext from './ThemeContext';
import ThemeBar from './components/ThemeBar';
import TodoList from './components/TodoList';
const themes={
	light:{
		classnames:"red",
		bgColor:'#F03',
		color:"#000"
	},	
	dark:{
		classnames:"blue",
		bgColor:'#69F',
		color:"#fff"
	},
}
class Welcome extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			theme: 'light',
			comments: ["","",""]
		}
		this.addComment=this.addComment.bind(this)
		this.changeTheme=this.changeTheme.bind(this)
	}
	addComment(comment){
      this.setState({
      	comments: [...this.state.comments,comment]
      })
	}
	changeTheme(theme){
      this.setState({
      	theme,
      })
	}	
	render(){
		const {comments}=this.state
		const todoList =['','','',''];
		const isLogin=true;
		const test=<h1></h1>
		return (
		  <ThemeContext.Provider value={themes[this.state.theme]}>
			<div className="App">
                <div className="App-header">
	                <h2></h2>
	                <a href="#theme-switcher" className="red" onClick={() => {this.changeTheme('light')}}></a>
	                <a href="#theme-switcher" className="blue" onClick={() => {this.changeTheme('dark')}}></a>
	                <ThemeBar/>
	                <DigitalClock />
	                <div className="section">
		                <div className="left">
			                <CommentBox commentLength={comments.length} onAddComment={this.addComment}/>
			                <CommentList  comments={comments} />
			            </div>
			            <div className="right">
		                    <TodoList />
		                </div>
	                </div>
                </div>
                <NameCard name={""} job={""} number={123456789} ishuman={true} tags={["","",""]}/>
                <NameCard name={""} job={""} number={123456789} ishuman={false} tags={["","","",""]}/>
                <NameCard name={""} job={""} number={123456789} ishuman={false} tags={["gay","",""]}/>
                <NameCard name={""} job={""} number={123456789} ishuman={false} tags={["","",""]}/>
                <div className="try">
	              	{4+3}{[1,2,3]}{<p></p>}
		            {isLogin? <p></p>:<p></p>}
		            {test}
		            <ul>
		                {todoList.map((item,index) =>
	                      <li key={index}>{item}</li>
		                )}
		            </ul>
		        </div>
            </div>
          </ThemeContext.Provider>
		);
	}
}

export default Welcome


